import Header from './Header'
import Footer from './Footer'

const Layout = (props) => (
  <div>
    <Header />
    {props.children}
    <Footer />
  </div>
)

Layout.getInitialProps = async () => {
  const res = await fetch('http://localhost:8888/wordpress-gatsby/wp-json/wp-api-menus/v2/menus/77');
  const data = await res.json();
  return {
    data
  }
}

export default Layout


